import React, { Component, PropTypes } from 'react'
import Header from './header';
import Footer from './footer';
import NewTemplateForm from './cabinetComponents/newTemplate';

export default class Cabinet extends Component {
    render() {
        return <div>
            <Header/>
            <NewTemplateForm/>
            <Footer/>
        </div>
    }
}


